*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  font-family: system-ui, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell,
    "Open Sans", "Helvetica Neue", sans-serif;
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 10px;
  padding-top: 80px;
}

img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

input,
button,
textarea,
select {
  font: inherit;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

#root,
#__next {
  isolation: isolate;
}

ul {
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  cursor: pointer;
  appearance: none;
  border: none;
  color: inherit;
}

/* Content styling */
:root {
  --radiant-bg: linear-gradient(160deg, #0093e9 0%, #80d0c7 100%);
}

.form-container,
.data-container {
  width: 500px;
  border-radius: 15px;
  background-image: var(--radiant-bg);
}

.form-container,
form,
.error {
  display: flex;
  justify-content: center;
  align-items: center;
}

.form-container {
  height: 80px;
}

form {
  gap: 10px;
  width: 100%;
  padding: 0 30px;
}

form > p {
  flex: 1;
}

button,
input {
  padding: 3px 10px 3px;
}

input {
  width: 100%;
  border: none;
}

.data-container {
  padding: 30px;
  height: 400px;
  color: #fff;
}

.data {
  display: flex;
  flex-direction: column;
  gap: 30px;
  padding-top: 10px;
}

.data div:first-child > p {
  display: flex;
  gap: 7px;
}

.data div:first-child > p:first-child {
  padding-bottom: 3px;
}

.data div:nth-child(2) {
  display: flex;
  justify-content: space-between;
}

.data div:nth-child(2) > img {
  margin-right: 40px;
}

h1,
.data p {
  font-weight: 500;
}

h1 {
  border-bottom: 1px solid #ffffff5f;
  padding-bottom: 15px;
  display: flex;
  justify-content: center;
  font-size: 2.2rem;
}

h2 {
  font-size: 1.7rem;
}

h3,
.error {
  font-size: 1.2rem;
}

.error {
  width: 500px;
  height: 50px;
  color: #000;
}
